<p>To Nahee as a kid, the fact that ship and of belly share the same characters in the Korean word bae, represented a significant truth. This was so obvious that when she finally learned that they diverge as ship and belly in English, she gasped in surprise to the extent of a slight hyperventilation. What a betrayal! Everyone knew about it, except me? Everyone was aware that this bae and the other bae were two completely different things? (Meanwhile, the edible bae [pear] was not even on question.) Dizzy and suffocatedjust slightlyyoung Nahee determined to conclude that if it were true that humans are such outrageous beings, she would never allow any of them to be carried in her bae.</p>
